var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"bed-page"},[_c('div',{staticClass:"box-text"},[_vm._v(" Majiong Empire on Web（麦囧战国web版，以下简称MEW，原麦囧战国桌面版简称ME）是我自创的一款桌面游戏的web化延伸，起源自我初高中时期的想法。在那个时期，很多小伙伴喜欢跟我一起玩耍，我会在玩耍时间里面总是设想一些新奇的游戏与伙伴一起游乐。而这款游戏的起源就是那时候我使用麻将充当小兵的一种角色扮演游戏。 ")]),_c('div',{staticClass:"box-text"},[_vm._v(" 在游戏的持续“迭代进化”中，我逐渐尝试并加入了“兵种”，“克制”，“货币”，“攻守”等各种新奇的玩法。这些东西奠定了ME的基本游戏风格，因为使用麻将来游戏的过程中，血量，攻击力等一些复杂的数字很难精确地量化地记录下来，于是我开始有了其他改进想法。 ")]),_c('div',{staticClass:"box-text"},[_vm._v(" 为了改善血量，攻击力等令人困惑的“感觉式”计算问题，并且统一规范化游戏规则。我编写完善了ME作为桌面游戏所具备的所有游戏规则，并编写成说明书形式。另外，还完善了桌面游玩时所需的所有货币，卡牌的样式，并打印剪切成实物进行游玩。 ")]),_c('div',{staticClass:"box-text"},[_vm._v(" 但是，真实游戏的情况，反而让人更加疲惫。因为游戏规则的明确化，数字化，执行起来需要耗费更多的精力，虽然玩起来有趣，却极其耗费时间，这些困难成为了我真正想把ME web化的动力。 ")]),_c('div',{staticClass:"box-text"},[_vm._v(" 在我具备了web前端开发技能后，再次审视桌面ME的时候，发现将其战斗部分web化是一件令人兴奋的事情。所有枯燥麻烦的数值，计算，记录可以交给最擅长于此的计算机。于是我迫不及待地开始了MEW的开发，那个时候，用的还是简单陈旧的jQuery, 配合HTML5的新audio特性，终于完成了现在看到的MEW。也正因为ME正式web化，加上我受到各种游戏熏陶，我加入了更加丰富的系统设定和内容（英雄，技能等）。 ")]),_c('div',{staticClass:"box-text"},[_vm._v(" 虽然现在MEW的整体设计和实现已显陈旧，很多地方也可以优化 ")]),_c('div',{staticClass:"box-text"},[_vm._v(" 注意，MEW只是ME战斗部分的web化补充，游戏并不能体验到ME作为桌面游戏所具备的其他部分。但是，光战斗部分的内容，我认为已经足够让你拥有尝试的理由了。 ")]),_c('div',{staticClass:"box-btn"},[_c('div',{staticClass:"big-btn",on:{"click":_vm.go}},[_vm._v(" 开始尝试 ")])])])}
